@import 'advantages.scss';
@import 'header.scss';
@import 'video_slice.scss';
@import 'testimonial.scss';
@import 'call_to_action.scss';
@import url("https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap");
@import url('https://fonts.googleapis.com/css?family=Inter:400,500,600,700&display=swap');
:root {
  --primary: 255, 255, 255;
}
html,
body {
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}
html {
  font-size: 16px;
}
p {
  color: #bababa;
  line-height: 30px;
  font-weight: 300;
  font-size: 18px;
}
body {
  background-color: #0e0e0e;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  line-height: 1.8;
  color: white;
}
.wrapper {
  margin: 12vh auto;
  padding: 0 3em;
  max-width: 1146px;
}
html {
  font-size: 112.5%;
}
body {
  font-weight: 400;
  line-height: 1.65;
  color: #fff;
}
p {
  margin-bottom: 1.15rem;
}
h1,h2,h3,h4,h5 {
  margin: 2.75rem 0 1.05rem;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  line-height: 1.15;
}
h1 {
  font-style: normal;
  line-height: 110%;
  color: #fff;
  margin: 0;
  margin-top: 0;
  font-size: 3em;
  font-weight: 500;
}
h2 {
  font-style: normal;
  line-height: 140%;
  color: #fff;
  margin: 0;
  font-size: 2.5em;
  font-weight: 500;
}
h3 {
  font-style: normal;
  line-height: 140%;
  color: #fff;
  margin: 0;
  font-size: 2.3em;
}
h4 {
  font-size: 1.777em;
}
h5 {
  font-size: 1.333em;
}
small,
.text_small {
  font-size: 0.75em;
}
footer{
  background-color: #191a1b;
  text-align: center;
  padding: 40px;
  font-size: 14px;
  color: #656565;
}
@media (max-width: 1024px) {
  html {
    font-size: 80%;
  }
}